@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: grid;
  place-items: center;
  vertical-align: middle;
  height: 100vh;
}

:root {
  --lightest-grey: #dfdede;
  --light-grey: #bdbec0;
  --black: #060709;
  --low-black: #363e3c;
  --lower-black: #5d5f63;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif !important;
  -webkit-tap-highlight-color: transparent;
}

html,
body {
  min-height: 100vh;
  background-color: #bdbec0;
  background-color: var(--light-grey);
  font-size: 62.5%;
  font-weight: 500;
}

.display {
    color: #363e3c;
    padding-right: 1.9rem;
    font-size: 3.95rem;
    width: 26rem;
    white-space: nowrap;
    overflow: hidden;
    border-left:1.9rem solid transparent;
    position: absolute;
    top:10%;
    text-align: left;
}

.main-display{
    align-items: center;
    vertical-align: center;
    display: flex;
    height: 6rem;
    background-color: #bcbd95;
    padding: 0 1rem;
    width: 100%;
    justify-content: flex-end;

}
.btn {
  background-color: var(--black);
  padding: 1rem;
  color: white;
  border-radius: 50%;
  font-size: 2rem;
  height: 4rem;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  margin-left: 2rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin-bottom: 2rem;
}

.btn:hover,
.btn:active,
.btn:focus {
  background-color: var(--lower-black);
  transition: background-color ease-in-out 0.2s;
  outline: none;
  border: none;
}

.keypad {
  background-color: var(--low-black);
  padding: 2rem;
  border-radius: .5rem;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  border-top: .1rem solid white;
  outline: none;
  position: absolute;
  bottom: 0%;
}

.keypad-row {
  display: flex;
  flex-direction: row;
  margin-left: -2rem;
}

.keypad-row:nth-child(5) {
  margin-bottom: -2rem;
}

.keypad-row:nth-child(1) .btn {
  color: #08e8de;
}


.calc-container {
  height: 72vh;
  background-color: var(--black);
  position: relative;
  width: 26rem !important;
  border-radius: 2rem;
  z-index: 1000;
  box-shadow: rgba(14, 30, 37, 1) 0px 2px 4px 0px, rgba(14, 30, 37, 1) 0px 2px 16px 0px;
  border-top: 2px solid white;
  border-bottom: 2px solid white;
  margin-top: -5rem !important;
}

.calc-display {
  height: 30%;
  text-overflow: clip;

  
}

.main-display {
  border-radius: 0.5rem;

}

.calc-keypad {
  height: 70%;
}

.footer{
    display: flex;
    justify-content: center;
    vertical-align: middle;
    align-items: center;
    position: absolute;
    bottom: 0%;
    width: 100%;
    background-color: var(--black);
    height: 8vh;
    font-size: 2rem;
    color: #8BD8BD;
    font-weight: 400;
    font-family: 'Space Mono', monospace !important;
}
